.ModalClick {
  cursor: pointer;
  width: 450px;
  height: 253px;
  overflow: hidden;
}

.ModalClick img {
  width: 450px;
  height: 253px;
  transition: 0.3s all;
}

.ModalClick:hover img {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  -webkit-filter: brightness(130%);
          filter: brightness(130%);
  overflow: hidden;
}

.ModalClickS {
  cursor: pointer;
  width: 273px;
  height: 273px;
  overflow: hidden;
  text-align: center;
  background-color: #151515;
}

.ModalClickS img {
  height: 100%;
  transition: 0.3s all;
}

.ModalClickS:hover img {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  -webkit-filter: brightness(110%);
          filter: brightness(110%);
  overflow: hidden;
}

.ModalClick img:hover .ModalBox {
  display: block;
  z-index: 21;
}

.ModalBox {
  width: 70%;
  justify-content: center;
  align-items: center;
  border: none;
}

.ModalBoxS {
  width: 33%;
  justify-content: center;
  align-items: center;
  border: none;
}

.ModalBox img {
  display: block;
  margin: 0;
}

.ModalBoxS img {
  display: block;
  margin: 0;
}

